<template>
  <div>
    <!-- 顶部标签 -->
    <van-nav-bar
      title="购物车"
      @click-left="Return"
      @click-right="Manage"
      class="bgc"
      :right-text="isManage?'管理':'完成'"
    >
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>

    <van-list
      v-model="loadObjects.loading"
      :finished="loadObjects.finished"
      :finished-text="loadObjects.finishedText"
      @load="onLoad"
    >
      <div class="shopcart">
        <div v-if="shopcartData.length == 0">
          <van-empty description="购物车空空如也，逛一逛" />
        </div>

        <div v-else>
          <!-- 确认地址 -->
          <div class="ress-box">
            <div class="fl ress-left">
              <div :class="aid==''?'':'notShow'">
                <h2>请添加地址</h2>
              </div>
              <div :class="aid==''?'notShow':''">
                <p class="clearfix">
                  <span class="fl">
                    <van-icon name="user-circle-o" />
                    {{ressData.name}}
                  </span>
                  <span class="fr">
                    <van-icon name="phone-o" />
                    {{ressData.tel}}
                  </span>
                </p>
                <div class="ress-text">
                  <van-icon name="location-o" />
                  {{ressData.area}}
                </div>
              </div>
            </div>
            <div class="fr ress-right" @click="goRess">
              <van-icon name="arrow" />
            </div>
          </div>
        </div>

        <!-- 商品列表 -->
        <div class="productes">
          <van-checkbox
            @change="simpleSelect"
            v-model="item.isCheck"
            shape="round"
            v-for="(item,index) in shopcartData"
            :key="index"
          >
            <van-card
              :num="item.count"
              :price="item.price"
              :desc="item.desc"
              :title="item.name"
              :thumb="item.small_img"
            >
              <template #footer style="width:100px">
                <van-stepper
                  v-model="item.count"
                  @change="modifyCount(item)"
                  input-width="40px"
                  button-size="32px"
                />
              </template>
            </van-card>
          </van-checkbox>
        </div>

        <!-- 提交订单 -->
        <div v-if="isManage">
          <van-submit-bar
            :disabled="isHasCheck"
            class="submit-bar"
            :price="total"
            button-text="去支付"
            button-type="info"
            @submit="orderQuery"
          >
            <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
          </van-submit-bar>
        </div>
        <div v-else>
          <van-submit-bar
            class="submit-bar submit-bar-delete"
            button-text="删除选择"
            button-type="info"
            @submit="removeMoreShopcart"
          >
            <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
          </van-submit-bar>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import "../assets/css/shopcart.less";
import AddressListVue from "../views/resViews/AddressList";
export default {
  data() {
    return {
      isManage: true,
      allCheck: true,

      // 地址信息
      ressData: {
        name: "",
        tel: "",
        area: "",
        aid: ""
      },

      // 总金额
      total: 0,

      // 列表懒加载
      loadObjects: {
        loading: false,
        finished: false,
        finishedText: "没有更多内容了"
      },

      // 购物车所有数据获取
      allShopcartData: [],

      // 懒加载时的数据获取
      shopcartData: [],
      loadCount: 8,

      // 是否禁用提交按钮
      isHasCheck: true,

      aid: "",

      // 地址信息显示
      isHasRess: false
    };
  },

  created() {
    this.getRessData();

    this.getShopcartData();
  },

  methods: {
    // 获取订单信息
    getRessData() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == 20000) {
            let ress = res.data.result[0];
            for (let k in this.ressData) {
              this.ressData[k] = ress[k];
            }

            this.ressData.area =
              ress.province + ress.city + ress.county + ress.addressDetail;

            this.aid = ress.aid;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    goRess() {
      this.$router.push({ name: "AddressList" });
    },

    // 订单确认
    orderQuery(ord) {
      if (this.aid == "") {
        this.$toast("请添加地址");
      } else {
        this.$toast("订单确认");

        // 获取产品id
        let sids = [];

        this.shopcartData.forEach(v => {
          sids.push(v.sid);
        });

        let ordObj = {
          name: "Order",
          params: {
            sids,
            aid: this.ressData.aid
          }
        };

        this.$router.push(ordObj);
      }
    },

    // 全选
    allSelect() {
      this.shopcartData.forEach(v => {
        v.isCheck = this.allCheck;
      });

      this.allShopcartData.forEach(v => {
        v.isCheck = this.allCheck;
      });
    },

    // 单选
    simpleSelect() {
      this.sum();
      
      for (let i = 0; i < this.shopcartData.length; i++) {
        if (this.shopcartData[i].isCheck) {
        }
      }
    },

    // 单个删除
    removeShopcart(sid, index) {
      //获取购物车的sid
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sid)
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == 7000) {
            this.shopcartData.splice(index, 1);

            this.sum();

            this.$toast.success("删除成功");
          } else {
            this.$toast.success("删除失败");
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 多个删除
    removeMoreShopcart() {
      let sids = [];
      this.shopcartData.forEach(v => {
        if (v.isCheck) {
          sids.push(v.sids);
        }
      });

      //获取购物车的sid
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids)
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 7000) {
            //删除页面的购物车商品
            for (let i = this.shopcartData.length - 1; i >= 0; i--) {
              if (this.shopcartData[i].isCheck) {
                this.shopcartData.splice(i, 1);
              }
            }
            this.sum();
            this.$toast.success("删除成功");
          } else {
            this.$toast.success("删除失败");
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 返回上一页面
    Return() {
      this.$router.go(-1);
    },

    Manage() {
      this.isManage = !this.isManage;
    },

    // 获取购物车所有数据
    getShopcartData() {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      // 请求数据
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == 5000) {
            res.data.result.forEach(v => {
              v.isCheck = true;
            });

            this.allShopcartData = res.data.result;
            this.shopcartData = this.allShopcartData.splice(0, this.loadCount);

            this.sum();

            //根据截取购物车商品数量和总购物车商品数量判断是否加载完成
            if (this.allShopcartData.length > 0) {
              this.loadObjects.isLoading = false;
            } else {
              this.loadObjects.isLoading = true;
              this.loadObjects.isFinished = true;
            }
          }
        })
        .catch(err => {
          
        });
    },

    //懒加载购物车数据
    onLoad() {
      setTimeout(() => {
        this.shopcartData.push(
          ...this.allShopcartData.splice(0, this.loadCount)
        );

        if (this.allShopcartData.length == 0) {
          //没有数据可加载
          this.loadObjects.loading = true;
          this.loadObjects.finished = true;
        }
      }, 1000);
    },

    // 修改购物车数量
    modifyCount(item) {
      let tokenString = localStorage.getItem("__Tk");
      let inCheck = item.isCheck;

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid: item.sid,
          count: item.count
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 6000) {
            this.sum();
            item.isCheck = inCheck;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    sum() {
      this.total = 0;
      this.shopcartData.forEach(v => {
        if (v.isCheck) {
          this.total += v.count * v.price * 100;
        }
      });

      if (this.total == 0) {
        this.isHasCheck = true;
      } else {
        this.isHasCheck = false;
      }
    }
  }
};
</script>

